import React, { useEffect, useState } from 'react'
import Button from 'react-bootstrap/Button'
import Card from 'react-bootstrap/Card'
import "./style.css";
import { styled } from '@mui/material/styles';
import Rating from '@mui/material/Rating';
import FavoriteIcon from '@mui/icons-material/Favorite';
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';
import Typography from '@mui/material/Typography';
import { useDispatch } from 'react-redux';
import { ADD } from '../redux/actions/action';

const StyledRating = styled(Rating)({
    color: '#ff6d75',
 });
function Crad() {
  const dispatch = useDispatch();

  const send =(e)=>{

    dispatch(ADD(e));
  }
    const [product, setProduct] = useState([])
        const getUsers= async()=>{
        const respons = await fetch('https://fakestoreapi.com/products');
        setProduct ( await respons.json())
        }
useEffect(()=>{
getUsers();
},[])
  return (
    <div> 
      
      <div className=' mt-3'>
    <h2 className='text-center'>Add to Cart ....</h2>
    <div className="row d-flex justify-content-center align-items-center">
    {
          product.map((element, id) => {
            return (
              <>
                <Card key={element.id} style={{ width: '20rem',height:'20rem',border:"none" }} className=" mt-4 card_style">
                  <Card.Img variant="top" src={element.image} style={{width: '7rem',height:'7rem'}} className="mt-1" />
                  <Card.Body>
                    <h6>{element.title}</h6>
                    <Card.Text>
                    Price : ₹ {element.price}
                    </Card.Text>
                    <i>rating...</i>
                    <StyledRating
        value={element.rating.rate}
        icon={<FavoriteIcon fontSize="inherit" />}
        emptyIcon={<FavoriteBorderIcon fontSize="inherit" />}
      />
                    <hr />
                    <div className="button_div d-flex justify-content-center">
                    <Button variant="primary"  
                     onClick={()=> send(element)} className='col-lg-12'>Add to Cart</Button>
                    </div>
                  
                  </Card.Body>
                </Card>
                
              </>

            )
          })
        }

      </div>
    </div><br /></div>
  )
}

export default Crad