export const ADD = (item) => {
    return {
        type: "ADD_CART",
        payload: item
    }
}
//////////////////////removing
export const DLT = (id) => {
    return {
        type: "REMOVE_CART",
        payload: id
    }
}
// remove individual iteam

export const REMOVE = (iteam) => {
    return {
        type: "RMV_ONE",
        payload: iteam
    }
}
