import React, { useEffect, useState } from 'react'
import Navbar from 'react-bootstrap/Navbar'
import Badge from '@mui/material/Badge';
import Container from 'react-bootstrap/Container'
import Nav from 'react-bootstrap/Nav'
import { NavLink } from 'react-router-dom';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import { useDispatch, useSelector } from 'react-redux';
import { DLT } from '../redux/actions/action';
import { ADD } from '../redux/actions/action';
import { REMOVE } from '../redux/actions/action'
import Table from 'react-bootstrap/esm/Table';
function Header() {
  const remove = (item) => {
    dispatch(REMOVE(item))
  }
  const send = (e) => {

    dispatch(ADD(e));
  }
  const getdata = useSelector((state) => state.cartreducer.carts);
  console.log(getdata)
  const dispatch = useDispatch();
  const dlt = (id) => {
    dispatch(DLT(id))
  }
  const [price, setPrice] = useState(0);
  console.log(price);
  const [anchorEl, setAnchorEl] = useState(null);
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  const total = () => {
    let price = 0;
    getdata.map((ele, k) => {
      price = ele.price * ele.qnty + price
    });
    setPrice(price);
  };
  useEffect(() => {
    total();
  }, [total])
  return (
    <div>
      <Navbar bg="dark" variant="dark" style={{ height: '60px' }}>
        <Container>
          <NavLink to="/" className="text-decoration-none text-light mx-3">
            <Navbar.Brand href="#home">
              <img
                alt=""
                src="https://i.pinimg.com/originals/72/96/08/729608d3758eb6928e690689c54a8a30.gif"
                width="50"
                height="30"
                className="d-inline-block align-top"
              />{' '}
              Gupta's Cart
            </Navbar.Brand></NavLink>

          <Nav className="me-auto">
            <NavLink to='/' className="text-decoration-none text-light">Home</NavLink>

          </Nav>
          <i class="fa-solid fa-circle-user text-light" style={{ fontSize: 30, cursor: "pointer", marginRight: 30 }}></i>
          <Badge badgeContent={getdata.length} color="primary" id="basic-button"
            aria-controls={open ? 'basic-menu' : undefined}
            aria-haspopup="true"
            aria-expanded={open ? 'true' : undefined}
            onClick={handleClick}>

            <i class="fa-solid fa-cart-shopping text-light " style={{ fontSize: 30, cursor: "pointer" }}></i>
          </Badge>
        </Container>
        <Menu
          id="basic-menu"
          anchorEl={anchorEl}
          open={open}
          onClose={handleClose}
          MenuListProps={{
            'aria-labelledby': 'basic-button',
          }}
        >

          {
            getdata.length ?
              <div className='card_details' style={{ width: "24rem", padding: 10 }}>
                <Table>
                  <thead>
                    <tr>
                      <th>Photo</th>
                      <th>Product Name...</th>
                    </tr>
                  </thead>
                  <tbody>
                    {
                      getdata.map((e) => {
                        return (
                          <>
                            <tr>
                              <td>
                                <NavLink to={`/cart/${e.id}`} onClick={handleClose}>
                                  <img src={e.image} style={{ width: "5rem", height: "5rem" }} alt="" />
                                </NavLink>
                              </td>
                              <td>
                                <p>{e.title}</p>
                                <p>Price : ₹{e.price}</p>
                                <p>Quantity : {e.qnty}</p>
                                     <div className='mt-5 d-flex justify-content-between align-items-center' style={{ width: 100, cursor: "pointer", background: "#ddd", color: "#111" }}>
                                  <span style={{ fontSize: 24 }} onClick={e.qnty <= 1 ? () => dlt(e.id) : () => remove(e)}>-</span>
                                  <span style={{ fontSize: 22 }}>{e.qnty}</span>
                                  <span style={{ fontSize: 24 }} onClick={() => send(e)}>+</span>

                                </div>
                                <p style={{ color: "red", fontSize: 20, cursor: "pointer" }}  >
                                  <i className='fas fa-trash smalltrash' ></i>
                                </p>
                              </td>

                              <td className='mt-5' style={{ color: "red", fontSize: 20, cursor: "pointer" }} onClick={() => dlt(e.id)} >
                                <i className='fas fa-trash largetrash'></i>
                              </td>
                            </tr>
                          </>
                        )
                      })
                    }
                    <p className='text-center'>Total :₹ {price}</p>
                  </tbody>
                </Table>
              </div> :
              <div className="card_details d-flex justify-content-center align-items-center" style={{ width: "24rem", padding: 2, position: "relative" }}>
                <i class="fas fa-close smallclose" style={{ position: "absolute", top: 2, right: 20, fontSize: 30, cursor: "pointer" }} onClick={handleClose}></i>

                <img style={{ height: 250, padding: 5 }} src="https://mir-s3-cdn-cf.behance.net/project_modules/max_1200/dab60938212491.5968c68fa9113.gif" alt="" srcset="" />
                <p style={{ fontSize: 20 }}>Cart  Empty...</p>
              </div>
          }

        </Menu>

      </Navbar>

    </div>
  )
}

export default Header